.user__sex-back-m {
  position: relative;
  overflow: hidden;
  padding: 10px;
}

.user__sex_wrap {
  position: absolute;
  left: 0;
  top: 0;
  height: 20px;
  border-radius: 20px; 
  background-image: linear-gradient(223deg, #2af8d1, #10cedc 70%, #05bde0);
}
.user__sex_femal {
  background-image: linear-gradient(315deg, #fe8b9b, #ff73c1);
}

